  border-radius: 50%;
  border: 6px solid #90caf9;
  position: relative;
  transition: transform 2.5s ease-out;
  background: conic-gradient(
    #f48fb1 0deg 60deg,
    #ce93d8 60deg 120deg,
    #81d4fa 120deg 180deg,
    #aed581 180deg 240deg,
    #ffcc80 240deg 300deg,
