/* MAIN WRAPPER */
.booth-wrapper {
    text-align: center;
    background: transparent;
    padding: 60px 20px;
    font-family: 'Segoe UI', sans-serif;
  }
  
  .booth-title {
    font-size: 2.2rem;
    margin-bottom: 30px;
    color: #0288d1;
    font-family: 'Pacifico', cursive;
  }
  
  /* CAMERA AREA */
  .camera-area {
    position: relative;
    display: inline-block;
    width: -moz-fit-content;
    width: fit-content;
    padding: 8px;
    background: white;
    border-radius: 12px;
    overflow: hidden;
  }
  
  /* FILTERS */
  .filter-sepia video { filter: sepia(0.7); }
  .filter-cool video { filter: hue-rotate(180deg); }
  .filter-pink video { filter: hue-rotate(300deg) brightness(1.1); }
  .filter-vibrant video { filter: contrast(1.3) saturate(1.5); }
  .filter-grayscale video { filter: grayscale(1); }
  .filter-funky video { filter: hue-rotate(90deg) contrast(1.8); }
  .filter-dreamy video { filter: blur(2px) brightness(1.2); }
  
  /* FRAMES */
  
  .frame-bhargavi {
    border: 4px solid #ff69b4;
    box-shadow: 0 0 25px #ffb6c1;
  }
  
  .frame-confetti {
    border: 5px dashed #fdd835;
    animation: confetti-pulse 1s infinite alternate;
  }
  @keyframes confetti-pulse {
    from { box-shadow: 0 0 5px red; }
    to { box-shadow: 0 0 15px blue; }
  }
  
  .frame-myday {
    border: 6px double #4fc3f7;
    box-shadow: inset 0 0 10px #0288d1;
  }
  
  .frame-filmstrip {
    border: 10px solid black;
    background-image: repeating-linear-gradient(90deg, transparent, transparent 20px, #444 20px, #444 25px);
  }
  
  .frame-neon {
    border: 3px solid #00e5ff;
    box-shadow: 0 0 15px #00e5ff, 0 0 40px #00e5ff;
    animation: neon-glow 1.5s ease-in-out infinite alternate;
  }
  @keyframes neon-glow {
    from { box-shadow: 0 0 5px #00e5ff; }
    to { box-shadow: 0 0 20px #00e5ff, 0 0 60px #00e5ff; }
  }
  
  /* FEATURE TEXT */
  .feature-text {
    position: absolute;
    bottom: 8px;
    left: 50%;
    transform: translateX(-50%);
    color: #fff;
    font-size: 1.5rem;
    font-family: 'Dancing Script', cursive;
    background: linear-gradient(to right, #ff4081, #ff80ab);
    padding: 6px 20px;
    border-radius: 12px;
    box-shadow: 0 0 10px #ff80ab;
    animation: fadeText 2s infinite alternate;
  }
  @keyframes fadeText {
    from { opacity: 0.7; }
    to { opacity: 1; }
  }
  
  /* CONTROLS */
  .controls {
    margin-top: 30px;
  }
  .section {
    margin: 20px 0;
  }
  .controls button {
    margin: 6px;
    padding: 10px 18px;
    border: none;
    border-radius: 10px;
    background: #0288d1;
    color: white;
    cursor: pointer;
    font-weight: 500;
    transition: transform 0.2s ease;
  }
  .controls button:hover {
    transform: scale(1.05);
    background: #039be5;
  }
  
  /* CAPTURED OUTPUT */
  .capture-output img {
    width: 400px;
    border-radius: 12px;
    margin: 20px 0;
  }
  .capture-output button {
    margin: 10px;
    padding: 10px 20px;
    background-color: #4db6ac;
    color: white;
    border: none;
    border-radius: 12px;
    cursor: pointer;
  }
  
  /* BASE FEATURE TEXT STYLE */
.feature-text {
    position: absolute;
    bottom: 12px;
    left: 50%;
    transform: translateX(-50%);
    font-size: 1.6rem;
    font-weight: bold;
    pointer-events: none;
    white-space: nowrap;
  }
  
  /* ðŸŒ¸ Style 1 â€“ Elegant cursive with subtle glow */
  .feature-text.style1 {
    font-family: 'Dancing Script', cursive;
    color: #ff69b4;
    text-shadow: 1px 1px 2px #fff;
  }
  
  /* ðŸŒŸ Style 2 â€“ Gold sparkle with animation */
  .feature-text.style2 {
    font-family: 'Great Vibes', cursive;
    color: gold;
    animation: flicker 2s infinite alternate;
  }
  @keyframes flicker {
    from { opacity: 0.8; text-shadow: 0 0 5px gold; }
    to { opacity: 1; text-shadow: 0 0 10px orange; }
  }
  
  /* ðŸ’  Style 3 â€“ Modern neon blue outline */
  .feature-text.style3 {
    font-family: 'Poppins', sans-serif;
    color: #00e5ff;
    text-shadow: 0 0 5px #00e5ff, 0 0 10px #00bcd4;
  }
  
  /* ðŸ’œ Style 4 â€“ Soft purple with float effect */
  .feature-text.style4 {
    font-family: 'Pacifico', cursive;
    color: #ba68c8;
    animation: floatUp 3s ease-in-out infinite;
  }
  @keyframes floatUp {
    0% { transform: translateX(-50%) translateY(0); }
    50% { transform: translateX(-50%) translateY(-5px); }
    100% { transform: translateX(-50%) translateY(0); }
  }

