/* Optional Frost Overlay */
body::before {
  content: '';
  position: fixed;
  inset: 0;
  background: rgba(255, 255, 255, 0.1);
  z-index: -1;
  pointer-events: none;
}

/* Background movement animation */
@keyframes bgMove {
  0% { background-position: 0 0; }
  100% { background-position: 100px 100px; }
}

.balloon-game-container {
    text-align: center;
    padding: 40px;
    background: transparent;
    font-family: 'Poppins', sans-serif;
    min-height: 60vh;
  }
  
  .balloon-title {
    font-size: 2rem;
    color: #d32f2f;
    margin-bottom: 30px;
    animation: floatFadeIn 1s ease-in-out;
  }
  
  .balloon-row {
    display: flex;
    justify-content: center;
    grid-gap: 40px;
    gap: 40px;
    flex-wrap: wrap;
  }
  
  .balloon {
    width: 120px;
    height: 180px;
    cursor: pointer;
    position: relative;
    transition: transform 0.3s ease;
  }
  
  .balloon img {
    width: 100%;
    height: auto;
    transition: all 0.3s ease-in-out;
    animation: floatUp 2s ease-in-out infinite;
  }
  
  .balloon:hover {
    transform: scale(1.05);
  }
  
  .popped-photo {
    border-radius: 10px;
    box-shadow: 0 8px 16px rgba(0,0,0,0.2);
    animation: revealPhoto 0.5s ease forwards;
  }
  
  @keyframes floatUp {
    0%, 100% { transform: translateY(0); }
    50% { transform: translateY(-10px); }
  }
  
  @keyframes revealPhoto {
    from { opacity: 0; transform: scale(0.8); }
    to { opacity: 1; transform: scale(1); }
  }
  
  @keyframes floatFadeIn {
    from { opacity: 0; transform: translateY(20px); }
    to { opacity: 1; transform: translateY(0); }
  }
  
  .photo-modal {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 99999;
    width: 100vw;
    height: 100vh;
    background: rgba(0, 0, 0, 0.85);
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
  }
  
  .photo-modal img {
    max-width: 90vw;
    max-height: 90vh;
    border-radius: 12px;
    box-shadow: 0 0 20px rgba(255, 255, 255, 0.5);
    animation: zoomIn 0.3s ease-in-out;
  }
  
  @keyframes zoomIn {
    from {
      transform: scale(0.6);
      opacity: 0;
    }
    to {
      transform: scale(1);
      opacity: 1;
    }
  }
  
.gift-section {
  padding: 60px 20px;
  background: transparent;
  text-align: center;
}

.gift-title {
  font-family: 'Dancing Script', cursive;
  font-size: 2.5rem;
  color: #d84315;
  margin-bottom: 30px;
}

.gift-boxes {
  display: flex;
  justify-content: center;
  grid-gap: 60px;
  gap: 60px;
  flex-wrap: wrap;
}

.gift-box {
  width: 180px;
  height: 180px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  transition: transform 0.2s ease;
  position: relative;
}

.gift-box:hover {
  transform: scale(1.05);
}

.gift-box img {
  max-width: 100%;
  max-height: 100%;
}

.gift-image {
  width: 2200px;
  height: 220px;
  object-fit: contain;
}

.sparkle {
  position: absolute;
  width: 100px;
  height: 100px;
  top: -25px;
  right: -25px;
  pointer-events: none;
  z-index: 2;
}

.empty-msg {
  color: #ff5722;
  font-weight: bold;
  font-size: 1.2rem;
}

.reset-btn {
  margin-top: 40px;
  padding: 12px 24px;
  background: #ffcc80;
  color: #6d4c41;
  border: none;
  border-radius: 8px;
  font-size: 1.1rem;
  cursor: pointer;
  transition: background 0.3s;
}

.reset-btn:hover {
  background: #ffb74d;
}

.dumselods-container {
    padding: 50px 20px;
    text-align: center;
    background: rgba(255, 255, 255, 0.8);
    border-radius: 12px;
    max-width: 700px;
    margin: auto;
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.1);
  }
  
  .team-names input {
    margin: 10px;
    padding: 8px;
    font-size: 1rem;
    border-radius: 8px;
    border: 1px solid #ccc;
  }
  
  .scores {
    margin: 20px 0;
    font-size: 1.2rem;
  }
  
