  margin-bottom: 40px;
  color: #6a1b9a;
}

.dump-grid {
  column-count: 3;
  grid-column-gap: 15px;
  column-gap: 15px;
  padding: 0 10px;
}

.dump-item {
  page-break-inside: avoid;
  break-inside: avoid;
  margin-bottom: 15px;
  overflow: hidden;
