}

.truth-dare-container {
    padding: 50px 20px;
    background: radial-gradient(circle, #fdfbfb, #ebedee);
    border-radius: 20px;
    box-shadow: 0 12px 30px rgba(0, 0, 0, 0.15);
    max-width: 600px;
    margin: 60px auto;
    text-align: center;
    font-family: 'Poppins', sans-serif;
    animation: fadeIn 1s ease;
  }
  
  .truth-dare-container h2 {
    font-size: 2.5rem;
    color: #8e44ad;
    margin-bottom: 20px;
    font-family: 'Dancing Script', cursive;
  }
  
  .flip-btn {
    background-color: #6c5ce7;
    color: white;
    border: none;
    padding: 14px 28px;
    font-size: 1.1rem;
    border-radius: 8px;
    cursor: pointer;
    transition: background-color 0.3s ease;
    box-shadow: 0 4px 10px rgba(108, 92, 231, 0.4);
  }
  
  .flip-btn:hover {
    background-color: #a29bfe;
  }
  
  .result {
    margin-top: 25px;
    font-size: 1.6rem;
    color: #2d3436;
    animation: dropFade 0.8s ease;
  }
  
  .prompt-choice {
    margin-top: 20px;
    display: flex;
    justify-content: center;
    grid-gap: 20px;
    gap: 20px;
    flex-wrap: wrap;
  }
  
  .prompt-choice button {
    padding: 10px 20px;
    font-size: 1rem;
    border-radius: 6px;
    border: 2px solid #6c5ce7;
    background: white;
    cursor: pointer;
    transition: all 0.3s ease;
  }
  
  .prompt-choice button:hover {
    background: #dfe6e9;
    color: #6c5ce7;
  }
  
  .final-prompt-box {
    margin-top: 30px;
    padding: 20px;
    background: #ffeaa7;
    border-left: 6px solid #fdcb6e;
    border-radius: 12px;
    font-size: 1.2rem;
    color: #2d3436;
    animation: fadeInPrompt 0.5s ease;
  }
  
  /* Animations */
  @keyframes fadeIn {
    from { opacity: 0; transform: translateY(20px); }
    to { opacity: 1; transform: translateY(0); }
  }
  
  @keyframes dropFade {
    0% { opacity: 0; transform: translateY(-20px); }
    100% { opacity: 1; transform: translateY(0); }
  }
  
  @keyframes fadeInPrompt {
    0% { opacity: 0; transform: scale(0.9); }
    100% { opacity: 1; transform: scale(1); }
  }
  

  .coin-area {
    margin-top: 20px;
    display: flex;
    justify-content: center;
  }
  
  .coin-spin {
    width: 80px;
    height: 80px;
    animation: spinCoin 1.5s linear infinite;
  }
  
  @keyframes spinCoin {
    0% { transform: rotateY(0); }
    100% { transform: rotateY(360deg); }
  }
  
